.TodoItem {
  background-color: #FAFAFA;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 5px 5px 10px;
  border-radius: 5px;
}


.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  font-family: 'Chelsea Market', cursive;
}
.TodoItem-p--complete {
  text-decoration: line-through;

}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
  /* background-color: #CCC; */
}

.Icon-check {
  position: absolute;
  left: 12px;
}
.Icon-check:hover{
  color: #4caf50;
}
.Icon-check--active {
  color: #4caf50;
}

.Icon-delete {
  position: absolute;
  
  right: 0;
}
.Icon-delete:hover {
  color: red;
}
